import router from '../../router';
import ManageSections from '../../additional/SectionsGetter';
import i18n from '../../i18n';

export default {
  namespaced: true,
  moduleName: 'main',
  state: {
    manageSections: ManageSections,
    activeManageSection: undefined,
  },
  getters: {
    manageSections: (state) => state.manageSections.map(section => {
      return { ...section, ...{ name: i18n.t(section.name) } };
    }),
  },
  mutations: {
    setActiveManageSection: (state, path) => state.activeManageSection = path,
  },
  actions: {
    openSection: ({}, path) => {
      if (path === router.currentRoute.path) return;
      router.push(path);
    },
  },
};
